@import url(https://fonts.googleapis.com/css2?family=Karla&family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.errorRow {
  color: var(--main-text);
  font-weight: 800;
}

.errorText {
  color: var(--error);
}

.connect-to-yahoo a {
  border: 2px solid black;
  border-radius: 4px;
  padding: 10px;
  background-color: oldlace;
  color: black;
  font-weight: bold;
  text-decoration: none;
}

.connect-to-yahoo a:hover {
  background-color: var(--gradient1);
}

ul {
  display: flex;
  justify-content: space-around;
}

li.react-tabs__tab {
  padding: 10px 3vw 5px;
  position: relative;
  border: 1px solid var(--gradient1);
  border-radius: 8px 8px 0 0;
  border-bottom: none;
  bottom: 10px;
  cursor: pointer;
  background: whitesmoke;
}

.react-tabs__tab-panel {
  transition: background 0.2s ease;
  padding: 20px;
}

.react-tabs__tab:hover {
  background: var(--gradient1);
  transition: background 0.2s ease;
}

li.react-tabs__tab--selected:hover {
  cursor: auto;
}

li.react-tabs__tab--selected, .react-tabs__tab-panel {
  border: 1px solid transparent;
  background: var(--gradient1);
  transition: background 0.2s ease;
}

.navbar-tabs {
  padding: 20px 0 10px 0;
  font-size: 2rem;
  width: calc(100vw - 250px);
}

.react-tabs__tab--selected {
  font-weight: bold;
  position: fixed;
  /* top: 75px; */
}

.navbar-tabs > div {
  position: relative;
  width: 100%;
  top: -10px;
  /* overflow-y: scroll; */
}

table {
  border-spacing: 0;
  border: 2px solid black;
  border-radius: 4px;
  margin: 10px auto;
  font-size: 0.9rem;
}

tr:nth-child(odd) {
  background-color: rgba(164, 189, 205, 0.7);
}

tr:nth-child(even) {
  background-color: rgba(200, 233, 255, 0.7);
}

tr:hover {
  background-color: rgb(166, 220, 250);
}

table tr:last-child td {
  border-bottom: 0;
}

th, td {
  margin: 0;
  padding: 0.5rem;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
}

td:last-child, th:last-child {
  border-right: 0;
}

.pagination {
  padding: 0.5rem;
}

:root {
  --main-bg: #ffffff;
  --main-text:  #1b2733;
  --alt-text:  #e0e0e0;
  --gradient1: rgba(38, 38, 114, 0.3);
  --gradient2: rgba(56, 56, 141, 0);
  --gradient3: rgba(56, 100, 141, 0.3);
  --gradient4: rgba(56, 100, 141, 0);
  --main-dark: rgba(38, 38, 114, 0.8);

  --alt-bg: #637282;
  --h1-color: var(--alt-text);
  --header-bg: var(--main-dark);
  --alt-bg-opaque: rgba(38, 38, 114, 0.5);

  --header-text: #ffffff;
  --button: var(--main-dark);
  --eligible: #00ff99;
  --error: rgba(238, 11, 11, 0.651);
}

/* ðŸŽ¨ colors: #ffffff | #1b2733 | #637282 | #AB24A1 */

main {
  display: grid;
  height: 100vh;
  overflow-y: scroll;
  max-height: 95%;
  background: #ffffff;
  background: var(--main-bg);
  background:          
            linear-gradient(336deg, rgba(38, 38, 114, 0.3), rgba(56, 56, 141, 0) 70.71%),
            linear-gradient(127deg, rgba(56, 100, 141, 0.3), rgba(56, 100, 141, 0) 70.71%);
  background:          
            linear-gradient(336deg, var(--gradient1), var(--gradient2) 70.71%),
            linear-gradient(127deg, var(--gradient3), var(--gradient4) 70.71%);         
  grid-template-columns: minmax(100%, 10%);
  text-align: center;
  font-family: 'Karla', 'Rubik', sans-serif;
  /* position: relative; */
  min-width: 300px;
  margin: 0 auto;
  padding: 0;

  /* font-family: 'Rubik', sans-serif; */
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1 {
  margin: 50px auto;
  padding: 5px;
  background: rgba(38, 38, 114, 0.5);
  background: var(--alt-bg-opaque);
  max-width: 100%;
  color: #e0e0e0;
  color: var(--h1-color);
  font-size: 2rem;
}

h2 {
  background-color: transparent;
  border-radius: 8px 8px 0 0;
  font-size: 1.7rem;
  color: #ffffff;
  color: var(--header-text);
  margin: 0 auto;
  text-align: center;
  padding: 5px 5px;
}

h3 {
  color: #637282;
  color: var(--alt-bg);
  font-size: 1.6rem;
  margin: 30px 0 0;
  padding: 5px;
  width: auto;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#logout {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 99;
}

#chatbox {
  position: fixed;
  top: 50px;
  right: 0;
  margin: 0 0 0 10px;
  padding: 10px;
  border-left: 2px solid black;
  border-top: 2px solid black;
  border-radius: 4px 0 0 0;
  height: calc(100vh - 50px);
  width: 250px;
  background-color: whitesmoke;
}

#chat-messages {
  position: fixed;
  display: flex;
  justify-content: left;
  top: 50px;
  right: 0;
  width: 245px;
  height: calc(100vh - 110px);
  padding: 5px;
  margin: 2px;
  word-wrap: break-word;
  overflow: scroll;
}

.user {
  color: blue;
  font-weight: bold;
  font-size: 0.6rem;
}

.message {
  word-break: break-all;
  word-break: break-word;
  font-size: 0.8rem;
}

#messageInput {
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: 230px;
  padding: 5px;
}

#send-message-button {
  position: fixed;
  right: -200px;
  /* display: none; */
}

button {
  border-radius: 4px;
  border: 1px solid rgba(38, 38, 114, 0.8);
  border: 1px solid var(--button);
  color: white;
  background: rgba(38, 38, 114, 0.3);
  background: var(--gradient1);
}

.button:hover {
  background: rgba(56, 100, 141, 0.3);
  background: var(--gradient3);
}

